/*
 * Website Animation definitions
 */

@keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes spin {
  100% {
    transform: rotate(360deg);
  }
}

@keyframes slidein {
  from {
    top: -100%;
  }
  to {
    top: 0;
  }
}

.slidein {
  animation: slidein 0.5s;
}

@keyframes slide_center_left {
  to {
    transform: translateX(-100%);
  }
}

@keyframes slide_center_right {
  from {
    transform: translateX(0%);
  }
  to {
    transform: translateX(100%);
  }
}

@keyframes slide_right_center {
  from {
    transform: translateX(100%);
  }
  to {
    transform: translateX(0%);
  }
}

@keyframes slide_left_center {
  from {
    transform: translateX(-100%);
  }
  to {
    transform: translateX(0%);
  }
}
