/*
 * Abstracts Index
 */
/*
 * Reset SCSS file
 * CSS resets
 */
thead,
tbody,
tfoot, table, input,
label,
button,
select,
textarea, ol,
ul,
dd, a, header,
nav,
section,
blockquote,
figure,
footer, body, html {
  margin: 0;
  padding: 0;
  border: none;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

*:disabled {
  background-color: whitesmoke;
}

body {
  color: black;
  line-height: 1.5;
  background-color: white;
  font-size: inherit;
  font-family: Roboto, "Segoe UI", sans-serif;
  font-style: normal;
}

header,
nav,
section,
blockquote,
figure,
footer {
  font-size: 100%;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin-top: 0;
  color: inherit;
  margin-bottom: 0.5rem;
  font-family: Inter-bold;
  font-style: normal;
  font-weight: 400;
  line-height: 1.2;
}

h1 {
  font-size: 2em;
}

h2 {
  font-size: 1.5em;
}

h3 {
  font-size: 1.2em;
}

p {
  margin: 0;
  font-size: 1rem;
  line-height: 1.8rem;
}

a {
  color: inherit;
  text-decoration: none;
  cursor: pointer;
}

ol,
ul,
dd {
  list-style: none;
}

img,
svg {
  vertical-align: middle;
}

input,
label,
button,
select,
textarea {
  text-transform: none;
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
}

textarea:focus-visible,
input:focus-visible,
button:focus-visible {
  outline: none;
  outline-offset: 0;
  box-shadow: 0 0 0.75rem rgba(0, 0, 0, 0.1);
}

button,
[role=button],
[role=submit],
[role=reset] {
  appearance: none;
  cursor: pointer;
  background-color: transparent;
}

textarea {
  resize: vertical;
}

select {
  word-wrap: normal;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

iframe {
  border: none;
}

[hidden] {
  display: none !important;
}

::file-selector-button {
  font: inherit;
}

/** Scrollbars style **/
/* width */
::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background: transparent;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #555;
}

