  text-transform: uppercase;
}
footer.page-footer h3::after {
  content: "";
  width: 60px;
  position: absolute;
  left: 0%;
  bottom: 0%;
  border-bottom: 5px solid steelblue;
}
footer.page-footer p {
  margin-bottom: 1.3em;
}
footer.page-footer li {
  margin-bottom: 10px;
}
footer.page-footer a::hover {
  color: steelblue;
}
footer.page-footer #contact_info {
  text-align: center;
  margin-top: 3.125rem;
}
footer.page-footer #contact_info span {
  padding-right: 20px;
}
footer.page-footer #contact_info span:last-child {
  padding: 0;
}
footer.page-footer #contact_info span i {
  padding-right: 10px;
  color: steelblue;
}
footer.page-footer #copyright {
  padding: 10px 0;
  background-color: steelblue;
  text-align: center;
}

#scrollToTopButton {
  width: 50px;
  height: 50px;
  display: flex;
  color: white;
  cursor: pointer;
  font-size: 2em;
  border-radius: 2rem;
  align-items: center;
  justify-content: center;
  background: steelblue;
  position: absolute;
  right: 0;
  bottom: 0;
}
@media screen and (max-width: 375px) {
  #scrollToTopButton {
    top: 25%;
    right: 2%;
  }
}
@media screen and (min-width: 375px) and (max-width: 600px) {
  #scrollToTopButton {
    right: 4%;
  }
}
@media screen and (max-width: 768px) {
  #scrollToTopButton {
    top: calc((100% - 50px) / 2);
  }
}
@media screen and (min-width: 1200px) {
  #scrollToTopButton {
    background: #1e1e1e;
  }
}

/*
 * Website Animation definitions
 */
@keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes spin {
  100% {
    transform: rotate(360deg);
  }
}
@keyframes slidein {
  from {
    top: -100%;
  }
  to {
    top: 0;
  }
}
.slidein {
  animation: slidein 0.5s;
}

@keyframes slide_center_left {
  to {
    transform: translateX(-100%);
  }
}
@keyframes slide_center_right {
  from {
    transform: translateX(0%);
  }
  to {
    transform: translateX(100%);
  }
}
@keyframes slide_right_center {
  from {
    transform: translateX(100%);
  }
  to {
    transform: translateX(0%);
  }
}
@keyframes slide_left_center {
  from {
    transform: translateX(-100%);
  }
  to {
    transform: translateX(0%);
  }
}
